import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot } from '@angular/router';
import { AlertController } from '@ionic/angular';
import { CoreException } from '@nequi/commons/exceptions';
import {
  ModuleConfigProvider,
  NavigatorProvider,
  TranslateProvider
} from '@nequi/features/commons';
import { HomeViewModel } from '@nequi/features/notifications/presentation/';
import {
  OfficeWithdrawalInteractor,
  ReverseBranchWithdrawalException
} from '@nequi/features/office-withdrawal/core';
import { CardModalService, LoadingService, ToastService } from '@nequi/ui';

import { MiddlewareConfig } from './middleware.config';

interface Regretted {
  regretted: boolean;
}

interface OfficeWithdrawalNotificationParmas extends Regretted {
  tourHowToDoIt: {
    lastTime: string;
    minValue: string;
  };
}

@Injectable()
export class OfficeWithDrawalMiddleware implements CanActivate {
  public viewModel = new HomeViewModel();
  private config = MiddlewareConfig;
  constructor(
    private loadingService: LoadingService,
    private toastService: ToastService,
    private alertController: AlertController,
    private translateProvider: TranslateProvider,
    private navigatorProvider: NavigatorProvider,
    private officeWithdrawalInteractor: OfficeWithdrawalInteractor,
    private readonly cardModalService: CardModalService,
    private moduleConfigProvider: ModuleConfigProvider,
    private router: Router
  ) {}

  public async canActivate(_route: ActivatedRouteSnapshot, _state: RouterStateSnapshot) {
    await this.translateProvider.loadModule(this.moduleConfigProvider.i18n.moduleKey);

    const params = this.router.getCurrentNavigation().extras
      .state as OfficeWithdrawalNotificationParmas;
    const messages = this.translateProvider.get(this.config.i18n.alerts);

    if (params?.tourHowToDoIt) {
      return this.navigatorProvider.navigateForward(this.config.routes.howDoIDoIt);
    }

    if (params?.regretted) {
      const alert = await this.alertController.create({
        header: messages.title,
        cssClass: 'nequi-alert',
        message: messages.content,
        buttons: [
          {
            text: messages.buttons.cancel,
            cssClass: 'normal-font-weight',
          },
          {
            text: messages.buttons.accept,
            handler: async () => this.executeReverse(),
          },
        ],
      });

      await alert.present();

      return false;
    }

    this.navigatorProvider.navigateForward(this.config.routes.officeWithdrawal);
  }

  private async executeReverse() {
    const message = this.translateProvider.get(this.config.i18n.toastsSuccess);

    try {
      await this.loadingService.show();
      await this.officeWithdrawalInteractor.reverse();
      await this.loadingService.hideWithoutFeedback();
      await this.toastService.showSuccess(message.regretted.content);
      this.navigatorProvider.navigateRoot(this.config.routes.backpack);
    } catch (error) {
      this.processErrors(error);
    }
  }

  private async processErrors(error: CoreException) {
    this.loadingService.hideWithoutFeedback();

    if (error.constructor.name === ReverseBranchWithdrawalException.name) {
      await this.onShowCardModal();

      return;
    }

    await this.toastService.showError(error.message);
  }

  private async onShowCardModal(): Promise<void> {
    const i18n = this.translateProvider.get(this.config.i18n.modalRegretted);

    await this.cardModalService.show({
      imgPath: i18n.icon,
      title: i18n.header,
      content: i18n.message,
      buttons: [
        {
          text: i18n.buttons.next,
        },
        {
          text: i18n.buttons.recharge,
          color: 'medianoche',
          handler: async () => this.navigatorProvider.navigateRoot(this.config.routes.cashIn),
        },
      ],
    });
  }
}
