import { NgModule, Type } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { I18nModuleResolver } from '@nequi/features/commons';

import { OfficeWithDrawalConfig } from '../config/office-withdrawal.config';
import { OfficeWithDrawalMiddleware } from './middlewares/office-withdrawal.middleware';
import { OfficeWithdrawalPage } from './pages/home/office-withdrawal.page';
import { HowDoIDoItPage } from './pages/how-do-i-do-it/how-do-i-do-it.page';

const routes: Routes = [
  {
    path: '',
    canActivate: [OfficeWithDrawalMiddleware],
    component: Type,
  },
  {
    path: 'how-do-i-do-it',
    component: HowDoIDoItPage,
  },
  {
    path: 'home',
    component: OfficeWithdrawalPage,
  },
];

@NgModule({
  imports: [
    RouterModule.forChild(
      I18nModuleResolver.setup(routes, [OfficeWithDrawalConfig.i18n.moduleKey])
    ),
  ],
  exports: [RouterModule],
})
export class FeaturesOfficeWithdrawalRoutingModule {}
