import { FormControl, FormGroup, Validators } from '@angular/forms';
import { OfficeWithdrawalParams } from '@nequi/features/office-withdrawal/core';
import { ConfirmInfoItem } from '@nequi/ui';

import { OfficeWithdrawalConfig } from './office-withdrawal.config';

export class OfficeWithdrawalViewModel {
  public form: FormGroup;
  public amount: FormControl;
  public pocket: FormControl;
  public config = OfficeWithdrawalConfig;
  public minAmount: number;

  public initForm(officeWithdrawalParams: OfficeWithdrawalParams) {
    this.minAmount = Number(officeWithdrawalParams.minValue);
    this.buildFormGroup(officeWithdrawalParams);
  }

  public buildBottomsheetItems(messages: any): Array<ConfirmInfoItem> {
    return [
      {
        label: messages.product.label,
        value: messages.product.name,
      },
      {
        label: messages.amount,
        value: `$${this.amount.value.toLocaleString().replace(/,/g, '.')}`,
      },
      {
        label: messages.type.label,
        value: messages.type.name,
      },
    ];
  }

  private buildFormGroup(officeWithdrawalParams: OfficeWithdrawalParams) {
    this.createControls(officeWithdrawalParams);
    this.form = new FormGroup({
      amount: this.amount,
      pocket: this.pocket,
    });
  }

  private createControls(officeWithdrawalParams: OfficeWithdrawalParams) {
    this.pocket = new FormControl(null);
    this.amount = new FormControl(null, [
      Validators.required,
      Validators.min(Number(officeWithdrawalParams.minValue)),
      Validators.pattern(this.config.validators.multiplesOfFifty),
    ]);
  }
}
