import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { AlertController } from '@ionic/angular';
import { CoreException } from '@nequi/commons/exceptions';
import { TransactionBasePage } from '@nequi/features/commons/presentation';
import { OriginMoneyControlComponent } from '@nequi/features/money-management/presentation';
import {
  OfficeWithdrawalInteractor,
  OfficeWithdrawalParams,
  OfficeWithdrawalTransaction,
  WithdrawalTimeOutException
} from '@nequi/features/office-withdrawal/core';
import { CardModalService, ConfirmInfoPocketProps } from '@nequi/ui';
import { openUrl } from '@nequi/utils/functions';
import { TranslatePipe } from '@ngx-translate/core';

import { OfficeWithdrawalConfig } from './office-withdrawal.config';
import { OfficeWithdrawalViewModel } from './office-withdrawal.view-model';

@Component({
  selector: 'nequi-office-withdrawal',
  templateUrl: './office-withdrawal.page.html',
  styleUrls: ['./office-withdrawal.page.scss'],
})
export class OfficeWithdrawalPage extends TransactionBasePage implements OnInit {
  @ViewChild(OriginMoneyControlComponent)
  public originMoneyControl: OriginMoneyControlComponent;

  public balanceToogle = false;
  public config = OfficeWithdrawalConfig;
  public viewModel = new OfficeWithdrawalViewModel();
  public cashoutLinkText: string;
  public minValueTextDescription: string;
  public minValueTextError: string;
  public isLoading: boolean;
  public officeWithdrawalParams: OfficeWithdrawalParams;

  constructor(
    injector: Injector,
    private officeWithdrawalInteractor: OfficeWithdrawalInteractor,
    private translatePipe: TranslatePipe,
    private alertController: AlertController,
    private cardModalService: CardModalService
  ) {
    super(injector);
  }

  public async ngOnInit() {
    await this.initForm();
  }

  public goBack() {
    this.navigatorProvider.back();
  }

  public whereToWithdrawMyMoney() {
    openUrl(this.cashoutLinkText);
    this.navigatorProvider.navigateRoot(this.config.routes.backpack);
  }

  public async setParamsText() {
    const minValueFormated = `$${this.officeWithdrawalParams.minValue
      .toLocaleString()
      .replace(/,/g, '.')}`;

    this.cashoutLinkText = this.translatePipe.transform(OfficeWithdrawalConfig.i18n.help.link, {
      cashoutLink: this.officeWithdrawalParams.cashoutLink,
    });

    this.minValueTextDescription = this.translatePipe.transform(
      this.config.i18n.mainPage.inputs.amount.description,
      { minValue: minValueFormated }
    );

    this.minValueTextError = this.translatePipe.transform(
      this.config.i18n.mainPage.inputs.amount.errors.minAmount,
      { minValue: minValueFormated }
    );
  }

  public async executeWithdraw() {
    const pocket = this.viewModel.pocket.value;

    const transaction = new OfficeWithdrawalTransaction()
      .withPocket(pocket)
      .withAmount(this.viewModel.amount.value);

    super.executeTransaction(transaction);
  }

  protected async setOriginMoney(
    transaction: OfficeWithdrawalTransaction
  ): Promise<OfficeWithdrawalTransaction> {
    if (transaction.pocket) {
      return transaction;
    }

    this.shouldSelectPocket = true;

    const pocket = await this.originMoneyService.openModal(this.officeWithdrawalParams.minValue);

    return new OfficeWithdrawalTransaction().withAmount(transaction.amount).withPocket(pocket);
  }

  protected async onTransactionSuccess() {
    this.showModal();
  }

  protected async onTransactionFailed(error: CoreException) {
    if (error.constructor.name === WithdrawalTimeOutException.name) {
      this.presentAlert();
    }

    await this.toastService.showError(error.message);
  }

  protected async getConfirmationInfo(): Promise<ConfirmInfoPocketProps> {
    const messages = this.translateProvider.get(OfficeWithdrawalConfig.i18n.boottomSheet);

    return {
      title: messages.confirm.title,
      subtitle: messages.confirm.subtitle,
      items: this.viewModel.buildBottomsheetItems(messages.confirm.items),
      confirmLabel: messages.confirm.accept,
    };
  }

  private async initForm() {
    this.isLoading = true;
    this.officeWithdrawalParams = await this.officeWithdrawalInteractor.getOfficeWithdrawalParams();
    this.viewModel.initForm(this.officeWithdrawalParams);
    this.setParamsText();
    this.isLoading = false;
  }

  private async showModal() {
    const amountFormated = `$${this.viewModel.amount.value.toLocaleString().replace(/,/g, '.')}`;
    const messages = this.translateProvider.get(OfficeWithdrawalConfig.i18n.transactionSuccess);
    const content = this.translatePipe.transform(
      OfficeWithdrawalConfig.i18n.modalTransactionParam,
      {
        amount: amountFormated,
      }
    );

    await this.cardModalService.show({
      title: content,
      imgPath: messages.icon,
      content: messages.message,
      buttons: [
        {
          text: messages.buttons.next,
          handler: () => this.navigatorProvider.navigateRoot(this.config.routes.backpack),
        },
        {
          isLink: true,
          text: this.translateProvider.get(OfficeWithdrawalConfig.i18n.help.label),
          handler: () => this.whereToWithdrawMyMoney(),
        },
      ],
    });
  }

  private async presentAlert() {
    const messages = this.translateProvider.get(OfficeWithdrawalConfig.i18n.errors);
    const alert = await this.alertController.create({
      header: messages.default.title,
      cssClass: 'nequi-alert',
      message: messages.default.content,
      buttons: [
        {
          text: messages.default.buttons.okButton,
          handler: () => {
            this.executeWithdraw();
          },
        },
      ],
    });

    await alert.present();
  }
}
