import { Component, Injector, OnInit } from '@angular/core';
import { NavigatorProvider } from '@nequi/features/commons';
import { FeatureBasePage } from '@nequi/features/commons/presentation';
import {
  OfficeWithdrawalInteractor,
  OfficeWithdrawalParams
} from '@nequi/features/office-withdrawal/core';
import { openUrl } from '@nequi/utils/functions';
import { TranslatePipe } from '@ngx-translate/core';

import { HowDoIdoItConfig } from './how-do-i-do-it.config';

@Component({
  selector: 'nequi-how-do-i-do-it',
  templateUrl: './how-do-i-do-it.page.html',
  styleUrls: ['./how-do-i-do-it.page.scss'],
})
export class HowDoIDoItPage extends FeatureBasePage implements OnInit {
  public config = HowDoIdoItConfig;
  public officeWithdrawalParams: OfficeWithdrawalParams;
  public lastTimeText: string;
  private cashoutLinkText: string;

  constructor(
    protected navigatorProvider: NavigatorProvider,
    private translatePipe: TranslatePipe,
    private officeWithdrawalInteractor: OfficeWithdrawalInteractor,
    injector: Injector
  ) {
    super(injector);
  }

  public async ngOnInit() {
    this.officeWithdrawalParams = await this.officeWithdrawalInteractor.getOfficeWithdrawalParams();
    this.setParamsText();
  }

  public goToSiteToWithdrawMyMoney() {
    openUrl(this.cashoutLinkText);
  }

  private setParamsText() {
    this.cashoutLinkText = this.translatePipe.transform(this.config.i18n.help.link, {
      cashoutLink: this.officeWithdrawalParams.cashoutLink,
    });

    this.lastTimeText = this.translatePipe.transform(
      this.config.i18n.howDoIDoItPage.firstStep.title,
      {
        lastTime: this.officeWithdrawalParams.lastTime,
      }
    );
  }
}
