import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { ReactiveFormsModule } from '@angular/forms';
import { ModuleConfigProvider } from '@nequi/features/commons';
import { FeaturesContactsModule } from '@nequi/features/contacts/presentation';
import { FeaturesMoneyManagementModule } from '@nequi/features/money-management/presentation';
import { FeaturesStyleManagerModule } from '@nequi/features/style-manager/presentation';
import { NequiComponentsModule, NequiUiModule } from '@nequi/ui';
import { TranslateModule } from '@ngx-translate/core';

import { OfficeWithDrawalConfig } from '../config/office-withdrawal.config';
